import React, {Component} from 'react';
import ReactDOM from 'react-dom';

let myNews = [
    {
        author: 'Саша Печкин',
        text: 'В четчерг, четвертого числа...',
        bigText: 'в четыре с четвертью часа четыре чёрненьких чумазеньких чертёнка чертили чёрными чернилами чертёж.'
    },
    {
        author: 'Просто Вася',
        text: 'Считаю, что $ должен стоить 35 рублей!',
        bigText: 'А евро 42!'
    },
    {
        author: 'Гость',
        text: 'Бесплатно. Скачать. Лучший сайт - http://localhost:3000',
        bigText: 'На самом деле платно, просто нужно прочитать очень длинное лицензионное соглашение'
    }
];

class Article extends Component{

    constructor(props){
        super(props);
        this.state = {
            visible: false
        };
    }

    readMoreClick(e){
        e.preventDefault();

        this.setState( {visible: !this.state.visible} );
    }

    render(){

        let author = this.props.data.author;
        let text = this.props.data.text;
        let bigText = '';

        if(this.state.visible)
            bigText = this.props.data.bigText;

        return(
          <div className="article">
              <h3>{author}</h3>
              <p>{text}</p>
              <a href="#" onClick={this.readMoreClick.bind(this)}>Read more...</a>
              <p>{bigText}</p>
          </div>
        );
    }
}

class News extends Component{
    render(){
        const data = this.props.data;
        let newsTemplate;

        if(data.length){
            newsTemplate = data.map((item,index)=>{
                return(
                    <Article key={index} data={ {author:item.author,text:item.text, bigText:item.bigText} }/>
                );
            });
        }
        else{
            newsTemplate = <p>No news yet</p>
        }

        return(
            <div className="news">
                {newsTemplate}
                <strong>Total news: {data.length}</strong>
            </div>
        );
    }
}

class SimpleInput extends Component{

    constructor(props){
        super(props);
        this.state = {
            name: '',
            text: '',
            check: false,
            inputName: false,
            inputText: false,
            buttonDisabled: true
        };
        this.onClickButton = this.onClickButton.bind(this);
    }

    componentDidMount(){
        ReactDOM.findDOMNode(this.refs.testInput).focus();
    }

    onChangeField(fieldName,e){

        let value = e.target.value;

        if(value.trim().length === 0) this.state[fieldName] = false;
        else this.state[fieldName] = true;

        if(this.state.inputText && this.state.inputName && this.state.check){
            this.setState({buttonDisabled : false});
            console.log('22222');
        }
        else{
            console.log('1111');
            this.setState({buttonDisabled : true});
        }

    }

    onClickButton(){

        const author = ReactDOM.findDOMNode(this.refs.testInput).value;
        const bigText = ReactDOM.findDOMNode(this.refs.testText).value;
        const text = bigText;
        myNews.push({
            author,
            text,
            bigText,
        });

        console.log(myNews);

        this.setState({
            bigText:''
        });
    }



    render(){
        return(
            <div className="inputNews">
                <input
                    className="inputName"
                    ref="testInput"
                    type="text"
                    onChange={this.onChangeField.bind(this,'inputName')}
                    placeholder='Enter value'
                />
                <textarea
                    className="inputText"
                    ref="testText"
                    cols="40"
                    rows="10"
                    onChange={this.onChangeField.bind(this,'inputText')}
                />
                <button onClick={this.onClickButton} disabled={this.state.buttonDisabled}>Ok</button>
                <input type="checkbox" onChange={this.onChangeField.bind(this,'check')}/>
            </div>
        );
    }
}

export default class App extends Component {
    render() {
        return (
            <div className="app">
                <h1>News app</h1>
                <SimpleInput/>
                <News data={myNews}/>
            </div>
        );
    }
}

